/* index.css */

/* Universal Styles */
body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background: url('/public/background.jpg') no-repeat center center fixed;
  background-size: cover;
  color: #333;
}

/* Auth Container Styles */
.auth-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.auth-box {
  background: rgba(255, 255, 255, 0.8);
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.auth-box h2 {
  margin-bottom: 20px;
}

.auth-box input {
  width: calc(100% - 20px);
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.auth-box button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.auth-box button:hover {
  background-color: #0056b3;
}

.auth-box p {
  margin-top: 20px;
}

.auth-box a {
  color: #007bff;
  text-decoration: none;
}

.auth-box a:hover {
  text-decoration: underline;
}

/* Dashboard Container Styles */
.dashboard-container {
  padding: 20px;
  text-align: center;
  background: rgba(255, 255, 255, 0.9);
  margin: 20px auto;
  max-width: 800px;
  border-radius: 10px;
}

.dashboard-container h2 {
  margin-bottom: 20px;
  color: #007bff;
}

.task-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.task-item {
  padding: 15px;
  background-color: #f8f9fa;
  border: 1px solid #ddd;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: background 0.3s;
}

.task-item:hover {
  background-color: #e9ecef;
}

.task-actions button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #007bff;
  margin-left: 8px;
}

.task-actions button:hover {
  color: #0056b3;
}
